import React, { useState } from 'react'
import axios from 'axios'
import { useHistory } from "react-router-dom"

export default function Login(props) {
    const history = useHistory()
    const [user, setUser] = useState({})

    const changeUserHandler = ({ target: { name, value } }) => setUser({ ...user, [name]: value })

    const onSubmitHandler = (e) => {
        e.preventDefault()
        console.log("click")
        axios.post("http://localhost:4000/user/login", { "email": user.email, "password": user.password })
        .then(data => {
            localStorage.setItem("token", data.data.token)
            props.loginFunction()
            props.location.state ? history.push(props.location.state) : history.push('/')})
        .catch(err => console.log(err.response))
    }
    return (
        <>
            <h1>Sign in</h1>
            <form onSubmit={(e) => onSubmitHandler(e)}>
                <input
                    id="email"
                    onChange={(e) => changeUserHandler(e)}
                    placeholder="Email Address"
                    name="email"
                />

                <input
                    onChange={(e) => changeUserHandler(e)}
                    name="password"
                    placeholder="Password"
                    type="password"
                    id="password"
                />

                <button type="submit">Sign In</button>
                <button onClick={()=> history.push('/forgotpassword')}>Forgot password?</button>
                <p>Don't have an account? <a onClick={()=> history.push("/register")}>Sign Up</a></p>
            </form>
        </>
    )
}