export default function FlightCard(props) {
    const history = useHistory()
    let segments = ""
    
    if (props.flight?.itineraries[0].segments.length > 1) {

        segments = <>  <div class="flight-row">
            <div class="flight-from">
                <span class="from-code">{props.flight?.itineraries[0].segments[0].departure.iataCode}</span>
                <span class="from-city">{Moment(props.flight?.itineraries[0].segments[0].departure.at).format('HH:mm')}</span>
            </div>
            <div class="plane">
                <img src="https://cdn.onlinewebfonts.com/svg/img_537856.svg" alt="" />
            </div>
            <div class="flight-to">
                <span class="to-code">{props.flight?.itineraries[0].segments[0].arrival.iataCode}</span>
                <span class="to-city">{Moment(props.flight?.itineraries[0].segments[0].arrival.at).format('HH:mm')}</span>
            </div>
        </div>
            <div class="flight-row">
                <div class="flight-from">
                    <span class="from-code">{props.flight?.itineraries[0].segments[1].departure.iataCode}</span>
                    <span class="from-city">{Moment(props.flight?.itineraries[0].segments[0].departure.at).format('HH:mm')}</span>
                </div>
                <div class="plane">
                    <img src="https://cdn.onlinewebfonts.com/svg/img_537856.svg" alt="" />
                </div>
                <div class="flight-to">
                    <span class="to-code">{props.flight?.itineraries[0].segments[1].arrival.iataCode}</span>
                    <span class="to-city">{Moment(props.flight?.itineraries[0].segments[0].arrival.at).format('HH:mm')}</span>
                </div>
            </div>
        </>

    } else {
        segments = <div class="flight-row">
            <div class="flight-from">
                <span class="from-code">{props.flight?.itineraries[0].segments[0].departure.iataCode}</span>
                <span class="from-city">{Moment(props.flight?.itineraries[0].segments[0].departure.at).format('HH:mm')}</span>
            </div>
            <div class="plane">
                <img src="https://cdn.onlinewebfonts.com/svg/img_537856.svg" alt="" />
            </div>
            <div class="flight-to">
                <span class="to-code">{props.flight?.itineraries[0].segments[0].arrival.iataCode}</span>
                <span class="to-city">{Moment(props.flight?.itineraries[0].segments[0].arrival.at).format('HH:mm')}</span>
            </div>
        </div>
    }

    const goCheckout = () =>{
        props.setCheckoutFlight(props.flight)
        history.push(`/flightcheckout`)
    }
    return (
        <>
            <div class="flight-card">
                <div class="flight-card-header">
                    <div class="flight-logo">
                        <img src="https://book.jetblue.com/assets/header/img/jetblue-white-reg.png" alt="" />
                    </div>
                </div>
                <div class="flight-card-content">
                    {segments}
                    <div class="flight-details-row">
                        <div class="flight-operator">
                            <span class="title">OPERATOR</span>
                            <span class="detail"></span>
                        </div>
                        <div class="flight-number">
                            <span class="title">PRICE</span>
                            <span class="detail">{props.flight?.price.grandTotal}</span>
                        </div>
                        <div class="flight-class">
                            <span class="title">CLASS</span>
                            <span class="detail">{props.flight?.travelerPricings[0].fareDetailsBySegment[0].cabin}</span>
                        </div>
                        <button onClick={() => goCheckout()}>Checkout</button>
                    </div>
                </div>
            </div>
        </>
    )
}
