import React, { useState } from 'react'
import { useHistory } from "react-router-dom"
import axios from 'axios'

export default function Profile(props) {
    const [user, setUser] = useState(props.user)
    const [visibility, setVisibility] = useState(true)
    const history = useHistory()
    console.log("user");
    console.log(user.name);

    const userChangeHandler = (e) => {
        let name = e.target.name
        let value = e.target.value
        console.log({ ...user, [name]: value });
        setUser({ ...user, [name]: value })
    }

    const userOnsubmitHandler = (e) => {
        e.preventDefault()
        axios.put(`http://localhost:4000/user/editprofile/${props.user._id}`,
            { user },
            { headers: { "token": localStorage.getItem("token") } })
            .then(data => {
                //   setFlage(true)
                //   setMessage(data.data.message)
                //   setSuccess(true)
                props.setUser(data.data.user)
                console.log(data)
                setVisibility(true)
            }).catch(error => {
                //   setMessage(error.response.data.message)
                //   setFlage(true)
                /* setSuccess(false) */
                console.log(error)
            })
    }
    
    return (
        <>
            <h1>Personal info</h1>
            <div style={{ borderBottom: "1px solid", marginBottom: "20px" }}>
                <p style={{ fontWeight: "bold" }}>Name</p>
                {visibility ?
                <div>
                    <p>{props.user.name}</p>
                    <a onClick={() => setVisibility(false)}>Edit</a>
                    </div>
                    :
                    <div>
                        <form onSubmit={(e) => userOnsubmitHandler(e)}
            >
                        <input
                        name="name"
                        id="name"
                        placeholder={props.user.name}
                        onChange={(e) => userChangeHandler(e)}/>
                        <button type="submit">Save</button>
                        </form>
                        <a onClick={() => setVisibility(true)}>cancel</a>
                    </div>
                    }
            </div>
            <div style={{ borderBottom: "1px solid", marginBottom: "20px" }}>
                <p style={{ fontWeight: "bold" }}>Email</p>
                <p>{props.user.email}</p>
            </div>
            <div style={{ borderBottom: "1px solid", marginBottom: "20px" }}>
                <p style={{ fontWeight: "bold" }}>Nationality</p>
                <p>{props.user.nationality}</p>
            </div>
            <div style={{ borderBottom: "1px solid", marginBottom: "20px" }}>
                <p style={{ fontWeight: "bold" }}>Address</p>
                <p>{props.user.address}</p>
            </div>
            <div style={{ borderBottom: "1px solid", marginBottom: "20px" }}>
                <p style={{ fontWeight: "bold" }}>Phone</p>
                <p>{props.user.phone}</p>
            </div>
            <button onClick={() => history.push('/changepassword')}>Update Password</button>
            <button onClick={() => history.push('/deleteaccount')}>Delete Account</button>
        </>
    )



}
