import React, { useState } from 'react'
import { useHistory } from "react-router-dom"

export default function Nav(props) {
  //    const [loginState, setLoginState] = setState()
  const history = useHistory()

 
  const logOut = () => {
    localStorage.removeItem("token")
    props.loginFunction()
    history.push("/")
  }


  const currencyChangeHandler = (e) => {
    let value = e.target.value
    console.log(`name value ${value}`)
    props.setCurrency(value)
}

  return (
    <>
      <nav>
        {/* Always */}
        <a onClick={() => history.push(`/`)}>home</a>
        <a onClick={() => history.push(`/flight`)}>flight</a>
        <a onClick={() => history.push(`/hotel`)}>hotel</a>


        {/* If login */}
        {props.isLogin ?
          <div>
            <a onClick={() => logOut()}>Logout</a>

            <a onClick={() => history.push(`/reservations`)}>Reservations</a>
            <a onClick={() => history.push(`/favorites`)}>Favorites</a>
            <a onClick={() => history.push(`/changepassword`)}>Change password</a>
            <a onClick={() => history.push(`/profile`)}>Profile</a>



          </div>
          :
          <a onClick={() => history.push(`/login`)}>Login</a>
        }

        <label htmlFor="currency">Currency</label>
        <select name="currency" id="currency" onChange={(e) => currencyChangeHandler(e)}>
          <option value="SAR">SAR</option>
          <option value="USD">USD</option>
          <option value="GB">GB</option>
        </select>
      </nav>
    </>
  )
}
