import React, { useState } from 'react'
import axios from 'axios'
import { useHistory } from "react-router-dom"

export default function Register() {
    const [user, setUser] = useState({}); // {}
    // for aleart styles
    const [flage, setFlage] = useState(false);
    const [success, setSuccess] = useState(false);
    const [message, setMessage] = useState("")

    const [password, setPassword] = useState({});
    
    const history = useHistory()


    const userChangeHandler = (e) => {
        let name = e.target.name
        let value = e.target.value
        console.log({ ...user, [name]: value });
        setUser({ ...user, [name]: value })
    }

    const userOnsubmitHandler = (e) => {
        e.preventDefault()

        if (user.password == user.confirm_password) {


            axios.post('http://localhost:4000/user/register',
            { name: user.name, email: user.email, password: user.password, phone: user.phone, birthDate: user.birthDate, nationality: user.nationality, address: user.address, })
            .then(data => {
                setFlage(true)
                setMessage(data.data.message)
                setSuccess(true)
                setTimeout(() => history.push('/login'), 2000)
                console.log(data);

            }).catch(error => {
                setMessage(error.response.data.message)
                setFlage(true)
                setSuccess(false)
                console.log(error)
            })

        } else {
            console.log("Password and confirm password are not equal");
        }

        
    
    }
    return (
        <div>
            <h1>Register</h1>
            <form onSubmit={(e) => userOnsubmitHandler(e)}
            >

                <input
                    name="name"
                    id="name"
                    placeholder="name"
                    onChange={(e) => userChangeHandler(e)}
                />
                <input
                    name="email"
                    id="email"
                    placeholder="email"
                    onChange={(e) => userChangeHandler(e)}
                />
                <input
                    name="nationality"
                    id="nationality"
                    placeholder="nationality"
                    onChange={(e) => userChangeHandler(e)}
                />
                <input
                    name="address"
                    id="address"
                    placeholder="address"
                    onChange={(e) => userChangeHandler(e)}
                />
                <input
                    name="password"
                    id="password"
                    placeholder="password"
                    type="password"
                    onChange={(e) => userChangeHandler(e)}
                />
                <input
                    name="confirm_password"
                    id="confirm_password"
                    placeholder="confirm password"
                    onChange={(e) => userChangeHandler(e)}
                />
                <input
                    name="phone"
                    id="phone"
                    placeholder="phone"
                    onChange={(e) => userChangeHandler(e)}
                />
                <input
                    name="birthDate"
                    id="birthDate"
                    placeholder="birthDate"
                    onChange={(e) => userChangeHandler(e)}
                />
                <button type="submit">Sign Up</button>

                <p>Already have an account?
                    <a onClick={() => history.push(`/login`)}>Sign in </a>
                </p>


            </form>


        </div>
    )
}
